import React from 'react'
import './style.css'
import vid from './image/pro.mp4'
import far from './image/farmer.jpg'
import xkcd from './image/XKCD.PNG'

export const Project = () => {
    return (
        <div>
            <div className="tag">
                <h1>Project</h1>
            </div>
            <video autoPlay loop muted className='video_edit'>
                <source src={vid} type='video/mp4' />
            </video>
            <div className='hpro'>
                <div className='outer'>
                    <img src={far} alt="" />
                    <div className="det">
                        <h2>Yard Managment System</h2>
                        <p>n this project, I have made a portal for farmers and yard workers to
                            understand the prices, goods and the basics of the yard for better understanding</p>
                    </div>
                </div>
                <div className='outer'>
                    <img src={xkcd} alt="" />
                    <div className="det">
                        <h2>XKCD Website</h2>
                        <p>In this Project, i am using HTML, CSS, JAVASCRIPT and core PHP for back-end. this site get email address from the user than send one
                            verification link on that user when user verified successfully thansend random comic from xkcd website it self duration 5 min Link is https://xkcd.ictmu.in/XKCD_comic<br />/rtcamp/index.php</p>
                    </div>
                </div>
                <div className='outer'>
                    <img src={far} alt="" />
                    <div className="det">
                        <h2>TO-DO LIST in React.js</h2>
                        <p>In this project, I made one of the web templet which is use for make
                            to-do list for the reminder.
                        </p>
                    </div>
                </div>
                <div className='outer'>
                    <img src={far} alt="" />
                    <div className="det">
                        <h2>Smart City project in SSIP Hackathon</h2>
                        <p>In this project, I have worked in a group in which I have to make a
                            smart door lock based on IoT and make a system that controls the
                            traffic of the road for not occurring an accident.</p>
                    </div>
                </div>

                <div className='outer'>
                    <img src={far} alt="" />
                    <div className="det">
                        <h2>Smart Street Light using IoT in SSIP Hackathon</h2>
                        <p>In this project, I have to make a street light based on IoT for saving
                            electricity. It works based on sensors like when a living organism
                            comes then light switches on automatically and not then switch it off
                            automatically. </p>
                    </div>
                </div>
                <div className='outer'>
                    <img src={far} alt="" />
                    <div className="det">
                        <h2>automatically. Number Plate Detection</h2>
                        <p>In this project, I have to make a system that works on collecting data
                            from the bus number plate by getting an image of it so bus conductors can get to know about the presence or absence of that
                            bus.</p>
                    </div>
                </div>
                <div className='outer'>
                    <img src={far} alt="" />
                    <div className="det">
                        <h2>Live camera Object Detection</h2>
                        <p>In this project, I have work on getting the information of the object or
                            living things which are shown in-camera for the recognition so that
                            the light instructor can get to know when to turn on the light or to
                            turn off the light</p>
                    </div>
                </div>
            </div>


        </div>
    )
}
