import { Language } from './components/Language';
import { Photo } from './components/Photo';
import { Project } from "./components/Project";
import { Footer } from './components/footer';
function App() {
    return (
        <div>
            <Header></Header>
            <Language></Language>
            <div className='pht'>
            <Photo></Photo>
            </div>
            <Project></Project>
            <Footer></Footer>
        </div>
    );

}
export default App;